.directory {
    background-color: rgb(30, 30, 30);
    outline-width: 5px;
    outline-color: black;
    width: 20%
}

.dirTitle {
    margin: 0;
    color: white;
    margin-bottom: 20px;
}

.hidden {
    display: none;
}

.fileOpen {
    outline: none;
    background-color: #4C50AF; /* Green */
    border: none;
    color: white;
    width: 100%;
    height: 5vh;
    justify-content: center;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin-right: 1px;
    margin-bottom: 15px;
    display: flex;
    align-items: center;
}



.fileList {
    list-style: none;
    align-items: flex-start;
}

.file {
    color:white;
    font-size: 16px;
}

.fileListContainer {
    text-align: left;
}

.jsLogo {
    width: 20px;
    height: 20px;
    margin-right:15px;
}

.fileCont {
    display:flex;
    margin-bottom: 5px;
}

.fileClick {    
    border:none;
    outline: none;
    background-color:rgba(255, 0, 0, 0);
    color:white;
}

.dirButtonCont {
    align-items: center;
    margin-bottom: 10px;
}

